* {
  margin: 0;
  padding: 0;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans", Arial, sans-serif;
  background: #f5f5f5; }

.container {
  max-width: 30em;
  margin: 1em auto;
  border-radius: 10px;
  box-shadow: 0px 0px 10px 0px #737373;
  background: white;
  padding-bottom: 1em; }

.header {
  text-align: center;
  border-radius: 10px 10px 0 0;
  padding: 1em 0 1em 0;
  background: linear-gradient(to right, #d4e4ef 0%, #86aecc 100%);
  text-shadow: 1px 1px white, 2px 2px #777;
  color: #333;
  transition: all 1s; }
  .header:hover {
    text-shadow: 1px 1px white, 2px 2px tomato;
    color: crimson;
    cursor: pointer; }

.search-wrapper {
  position: relative;
  display: flex;
  justify-content: center;
  margin: 0 2em 0 2em;
  background: white; }
  .search-wrapper form {
    background: white; }
  .search-wrapper .search-input {
    margin: 1em;
    padding-left: 1em;
    width: 25em;
    height: 3em;
    background: white;
    border: 1px solid #86aecc;
    border-radius: 5px; }
