* {
  margin: 0;
  padding: 0;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans", Arial, sans-serif;
  background: #f5f5f5; }

.container {
  max-width: 30em;
  margin: 1em auto;
  border-radius: 10px;
  box-shadow: 0px 0px 10px 0px #737373;
  background: white;
  padding-bottom: 1em; }

.header {
  text-align: center;
  border-radius: 10px 10px 0 0;
  padding: 1em 0 1em 0;
  background: -webkit-gradient(linear, left top, right top, from(#d4e4ef), to(#86aecc));
  background: -webkit-linear-gradient(left, #d4e4ef 0%, #86aecc 100%);
  background: linear-gradient(to right, #d4e4ef 0%, #86aecc 100%);
  text-shadow: 1px 1px white, 2px 2px #777;
  color: #333;
  -webkit-transition: all 1s;
  transition: all 1s; }
  .header:hover {
    text-shadow: 1px 1px white, 2px 2px tomato;
    color: crimson;
    cursor: pointer; }

.search-wrapper {
  position: relative;
  display: flex;
  justify-content: center;
  margin: 0 2em 0 2em;
  background: white; }
  .search-wrapper form {
    background: white; }
  .search-wrapper .search-input {
    margin: 1em;
    padding-left: 1em;
    width: 25em;
    height: 3em;
    background: white;
    border: 1px solid #86aecc;
    border-radius: 5px; }

.nav {
  padding: 1em 0 1em 0;
  background: white; }
  .nav ul {
    display: flex;
    flex-direction: row;
    list-style: none;
    justify-content: center;
    background: white; }
  .nav li {
    background: white; }
  .nav a {
    cursor: pointer;
    text-decoration: none;
    color: white;
    margin: 0 1em 0 1em;
    padding: 0.5em;
    border: 1px solid;
    border-radius: 5px;
    background: -webkit-gradient(linear, left top, right top, from(#fb9a89), color-stop(0%, #f9c1b9), to(#ef7b71));
    background: -webkit-linear-gradient(left, #fb9a89 0%, #f9c1b9 0%, #ef7b71 100%);
    background: linear-gradient(to right, #fb9a89 0%, #f9c1b9 0%, #ef7b71 100%); }

.history {
  position: relative;
  border-radius: 5px;
  padding: 1em;
  margin: 0 2em 0 2em;
  background: -webkit-gradient(linear, left top, left bottom, from(#f1ffcc), color-stop(31%, #f1ffcc), color-stop(58%, #d7f7b0), color-stop(74%, #c8f3a0));
  background: -webkit-linear-gradient(top, #f1ffcc 0%, #f1ffcc 31%, #d7f7b0 58%, #c8f3a0 74%);
  background: linear-gradient(to bottom, #f1ffcc 0%, #f1ffcc 31%, #d7f7b0 58%, #c8f3a0 74%); }
  .history ul {
    list-style: none;
    display: flex;
    flex-direction: row;
    justify-content: center;
    background: transparent; }
  .history li {
    margin: 0 0.5em 0 0.5em;
    cursor: pointer;
    background: transparent;
    color: #86aecc; }
  .history button {
    position: absolute;
    right: 0.2em;
    top: 0.2em;
    color: red;
    background-color: transparent;
    border: 0;
    cursor: pointer; }

.forecast-weather-info {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  margin: 2em;
  padding: 1em;
  background: -webkit-linear-gradient(315deg, #fdde9b 0%, white 0%, white 0%, #fffbf1 5%, #ffbe33 75%);
  background: linear-gradient(135deg, #fdde9b 0%, white 0%, white 0%, #fffbf1 5%, #ffbe33 75%);
  border-radius: 5px; }
  .forecast-weather-info div, .forecast-weather-info h2, .forecast-weather-info p, .forecast-weather-info ul {
    background: transparent; }
  .forecast-weather-info li {
    list-style-type: none;
    margin: 7px;
    background: transparent; }
  .forecast-weather-info .forecast-weather__img {
    margin: 0 4em 0 4em;
    background: transparent; }
  .forecast-weather-info .forecast-weather__description {
    font-size: 2em;
    background: transparent; }

.current-weather-info {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin: 1em;
  padding: 1em;
  border-radius: 5px;
  background: -webkit-linear-gradient(315deg, #fdde9b 0%, white 0%, white 0%, #fffbf1 5%, #ffbe33 75%);
  background: linear-gradient(135deg, #fdde9b 0%, white 0%, white 0%, #fffbf1 5%, #ffbe33 75%);
  color: #333333; }
  .current-weather-info .current-weather__main {
    background: transparent; }
    .current-weather-info .current-weather__main .current-weather__city {
      font-size: 2em;
      background: transparent; }
    .current-weather-info .current-weather__main .current-weather__temp {
      font-size: 4em;
      background: transparent; }
    .current-weather-info .current-weather__main img, .current-weather-info .current-weather__main .current-weather__description {
      background: transparent; }
  .current-weather-info .current-weather__more {
    background: transparent; }
    .current-weather-info .current-weather__more ul {
      list-style: none;
      background: transparent; }
    .current-weather-info .current-weather__more li {
      background: transparent; }

